.App {
  text-align: center;
  font-family: helvetica,courier,arial;
  background-color: #fff;
  margin-left:0;
}
.topStripe{
  width: 100%;
  height: 30px;
  background-color:#444;
  color:#fff !important;
  position: fixed;
  top: 0px;
  z-index: 1;
  margin-bottom: 30px;
}
#main{
  margin-top: 30px;
  background-color: #1C202A;
}

.firstCol{
  margin-left:0px !important;
  margin-right:0px !important;
}
h2{
  color:#444;
  padding-top:25px;
  padding-bottom:15px;
}
.header-logo-container {
  position: sticky;
  top: 0;
}
.logo{
  max-width: 100%;
}
.startSection  {
  height: 100%;
  margin-left:20%;
  margin-right:20%;
}

@media only screen and (max-width: 991px) and (min-width: 200px){
  .introHeader{
    font-size: 24;
    margin-top: 75px;
    padding-top: 40px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
    background-color: rgb(0, 4, 6 , .3) ;
  }
 }
 
 @media only screen and (max-width:3000px)  and (min-width: 991px){
  .introHeader{
    font-size: 72px;
    margin-top: 75px;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 40px;
    padding-right: 40px;
    background-color: rgb(0, 4, 6 , .3) ;
  }
 }


.introCard .col{
  width:100%;
  margin: 5px;
  /* margin-bottom: 30px; */
}
.introCardCol{
 margin-top: 150px;
 padding-top: 10px;
 margin-bottom: 20px;
 color:#444;

}
.Employees  {
  background-color: #fff;
  height: 100%;
  padding-bottom: 50px;
  padding-top: 30px;
  padding-left:20px;
  padding-right:20px;
}
.employeeCard{
  width:100%;
  margin-bottom: 20px;
  min-height:300px;
}
.employeeImage{
  max-height: 300px;
  max-width: 200px;
}

.Services  {
  
  padding-top: 30px;
  height: 100%;
  padding-left:10%;
  padding-right:10%;
  padding-bottom: 30px;
  border:0;

}
.Services h2{
  color:#fff;
}
.serviceCard{
  width:100%;
  margin-bottom: 20px;
  background-color: #000;
}
.serviceCard .card{
  border:none;
  
}
.Contacts{
  padding-top:20%;
  font-size: larger;
  color:#fff;
}
.Contacts h2{
  color:#fff;
}


.serviceCard .card-header{
  background-color: #444;
 color: #FFF;
 font-weight: 500;
}
.serviceCard .card-body{
  background-color: #282828;
 color: #FFF;
}


.contactInfo{
  top:50%;
  font-size: larger;
}


/**/
/*Strip the ul of padding and list styling*/
ul {
  list-style-type:none;
  padding-inline-start: 0;
}
/*Create a horizontal list with spacing*/
li {
  display:inline-block;
  margin-right: 1px;
}


/* Button */

.btn{
  
  border:none !important;
  transition:none !important;
}
.menuBtn{
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  border-radius: none;
  text-decoration: underline !important;
  
}
.menuBtn:active{
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  text-decoration: underline !important;
}
.btn-outline-light {
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  text-decoration: underline !important;
}
